export default {
	data() {
		return {
			sessionId:'',
			id:'',
			myData:{},
			disabled:false,
			keyNumberList:[1,2,3,4,5,6,7,8,9,0,'.','支付'],
			number:[],
			chat:[]
		}
	},
	onLoad(option) {
		this.sessionId = option.sessionId
		this.id = option.id
		this.init()
	},
	
	methods: {
		init(){
			uni.request({
				url:'https://www.nssnail.com/wx/getOrderDetail',
				data:{
					sessionId:this.sessionId,
					id:this.id
				},
				success: (res) => {
					if(res.data.stat == 1){
						this.myData = res.data.data
						if(this.myData.stat == 1){
							this.myData.stat = '未付款'
							this.disabled = false
						}else if(this.myData.stat == 2){
							this.myData.stat = '已付款(未发货)'
							this.disabled = false
						}else if(this.myData.stat == 3){
							this.myData.stat = '失效'
							this.disabled = true
						}else{
							this.myData.stat = '已发货'
							this.disabled = true
						}
					}else if(res.data.stat == 3){
						uni.showModal({
						    title: '提示',
						    content: '登录过期，请重新登录',
						    success: function (res) {
						        if (res.confirm) {
						            uni.navigateTo({
						            	url:'../myInit/myInit'
						            })
						        }
						    }
						});
					}
				}
			})
		},
		
		// 支付订单
		sure(){
			this.$refs.popup.open()
			this.number = []
			this.chat = []
		},
		
		// 提交密码
		submit(){
			
		},
		
		// 输入支付密码
		clickNum(index){
			if(index < 11){
				if(index < 10){
					index += 1
					if(index === 10){
						index -= 10
					}
					if(this.number.length < 6){
						this.number += index;
						this.chat += '.'
						return
					}
				}
			}else if(index == 11){
				if(this.number.length == 0){
					uni.showToast({
						title:'请输入支付密码',
						icon:'none',
						duration:3000
					})
				}else{
					var price = this.myData.price * this.myData.qty
					console.log(price,this.myData.orderNo)
					
					this.number.toString()
					console.log(typeof(this.number),this.number)
					uni.request({
						url:'https://www.nssnail.com/wx/userPay',
						method:'POST',
						data:{
							payPwd:this.number,
							price:price,
							sessionId:this.sessionId,
							orderNo:this.myData.orderNo
						},
						success: (res) => {
							if(res.data.stat == 1){
								uni.showToast({
									title:res.data.message,
									icon:'success',
									success:()=>{
										let that = this
										setTimeout(function(){
											that.init()
										},2000);
									}
								})
								this.$refs.popup.close()
								this.init()
							}else if(res.data.stat == 0){
								uni.showToast({
									title:res.data.message,
									icon:'none'
								})
							}else if(res.data.stat == 3){
							uni.showModal({
							    title: '提示',
							    content: '登录过期，请重新登录',
							    success: function (res) {
							        if (res.confirm) {
							            uni.navigateTo({
							            	url:'../myInit/myInit'
							            })
							        }
							    }
							});
						}
						}
					})
				}

			}
		}
	}
}
